.akses-container {
    /* border: 1px solid grey; */
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
}

.akses-box {
    background-color: white;
    border: 1px solid #fff;
    border-radius: 8px;
    padding: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.akses-box:hover {
    border: 1px solid #6a11cb;
    box-shadow: 0px 4px 8px #6b11cb40;
}

.akses-box h4 {
    font-size: 12px;
    /* border: 1px solid grey; */
    text-align: center;
    font-weight: bold;
}

.akses-icon {
    border: 1px solid #6b11cb0e;
    border-radius: 8px;
    color: white;
    font-size: 15px;
    padding: 10px;
    background: linear-gradient(90deg, #6a11cb, #ba81f7);
    box-shadow: 0px 4px 8px #6b11cb40;
}

