.form-card-design {
  padding: 5px;
  /* border: 1px solid #5D12EB; */
  border: 1px solid #ddd;
  border-radius: 6px;
  max-width: 800px;
  /* height: 500px; */
  margin: auto;
  background-color: #fff;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.5);
}

/* HEADER  */
.cc-head {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 5px;
  margin-bottom: 15px;
  border-bottom: 1px solid #ddd;
}

.cc-head .cc-title {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  color: #333;
}

.cc-title .cch-icon {
  border: 1px solid white;
  border-radius: 8px;
  padding: 5px;
  color: white;
  background: linear-gradient(90deg, #7246eb, #532189);
}

.mini-icon {
  font-size: 15px;
}

.cc-head p {
  font-size: 12px;
  font-weight: bold;
  margin: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.cch-icon {
  margin-right: 5px;
  font-size: 15px;
}

.cch-icon2 {
  font-size: 18px;
  cursor: pointer;
  border: 1px solid white;
  border-radius: 4px;
  color: #333;
}

.cch-icon2:hover {
  border: 1px solid #ddd;
  background-color: #ddd;
}

/* END HEADER  */

/* BODY  */
.fcd-content {
  /* border: 1px solid green; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  /* gap: 10px; */
}

.fcd-select-box {
  width: 100%;
  padding: 5px 10px;
  /* border: 1px solid magenta; */
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  /* margin-bottom: 10px; */
}

.fcd-select-box label {
  /* border: 1px solid red; */
  color: #333;
  width: 100%;
  text-align: left;
  margin: 0px;
  font-size: 13px;
  font-weight: bold;
}

.fcd-button {
  width: 90%;
  /* border: 1px solid red; */
}

/* ENND BODY  */

/* DROPDOWN  */
.fcd-dropdown {
  border-radius: 4px;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  /* padding: 2px; */
}

/* BUTTON  */
.fcd-select-box .fcd-btn {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px 7px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
  font-size: 12px;
}

.fcd-btn:hover {
  border: 1px solid #5D12EB;
}

/* INPUT SEARCH  */
.fcd-search-input {
  padding: 5px;
  font-size: 10px;
  color: #333;
  width: 95%;
  border-radius: 4px;
  outline: none;
  border: 1px solid #5D12EB;
  transition: border-color 0.2s ease;
}

.fcd-search-input:hover {
  border: 1px solid #5D12EB;
}

.fcd-search-input:focus {
  outline: none;
  border: 1px solid #5D12EB;
}


/* DROPDOWN BOARD  */
.fcd-menu {
  border: 1px solid #5D12EB;
  border-radius: 4px;
  position: absolute;
  background-color: white;
  color: #333;
  margin-top: 0px;
  box-shadow: 0px 4px 8px #6f30dc54;
  height: 25vh;
  width: 95%;
  top: 100%;
  overflow-y: auto;
  list-style: none;
  padding: 0px 5px;
}


/* DROPDOWN LIST  */
.fcd-select-list {
  width: 100%;
  margin-top: 10px;
  color: #333;
  /* border: 1px solid blue; */
}

.fcd-select-list .fcd-lists-btn {
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px 7px;
  background-color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  /* margin-left: 5px; */
  font-size: 12px;
  cursor: pointer;
}

.fcd-lists-btn:hover {
  border: 1px solid #5D12EB;
}

.ccl-menu {
  border: 1px solid #5D12EB;
  border-radius: 4px;
  box-shadow: 0px 4px 8px #6f30dc54;
  width: 95%;
  height: 20vh;
  overflow-y: auto;
  padding: 10px;
}

.ccl-menu li {
  border: 1px solid white;
  font-size: 12px;
  padding: 5px;
  cursor: pointer;
  background-color: white;
}

.ccl-menu li:hover {
  background-color: #5e12eb45;
}

/* BUTTON  */
.fcd-button .fcd-sub-btn {
  border: 1px solid #5D12EB;
  border-radius: 6px;
  background: linear-gradient(to right, #6a11cb, #2575fc);
  color: white;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 5px 7px;
  cursor: pointer;
  font-size: 15px;
  width: 100%;
}

.fcd-button .fcd-sub-btn:hover {
  background: linear-gradient(to right, #2575fc, #6a11cb);
  box-shadow: 0px 4px 8px #5e12eb46;
}

/* MEDIA QUERY  */
/* Large devices (desktops, 992px) */
@media (min-width: 992px) and (max-width: 1199.98px) and (min-height:600px) and (max-height:800px) {
  .wf-create {
    width: 30%;
  }
}

/* Medium devices (tablets, 768px) */
@media (min-width: 768px) and (max-width: 991.98px) and (min-height:500px) and (max-height:700px) {
  .wf-create {
    width: 40%;
  }
}

/* ukuran handpone besar */
@media (min-width: 576px) and (max-width: 767.98px) and (min-height:400px) and (max-height:700px) {
  .wf-create {
    width: 50%;
  }
}

/* ukuran handpone kecil */
@media (min-width:375px) and (max-width:575px) and (min-height:300px) and (max-height:600px) {
  .form-card-design {
    width: 100%;
    /* margin: 0; */
  }
}