.agenda-page-container {
  border: 1px solid #ddd;
  height: 91vh;
  padding: 0px;
  gap: 10px;
  margin-bottom: 10px;
  margin: 15px;
  background-color: #eee;
  border-radius: 8px;
  color: #333;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
}

/* HEADER  */
.agenda-page-container .agenda-page-header {
  /* border: 1px solid red; */
  width: 100%;
  padding: 10px;
  background-color: white;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  box-shadow: 0px 4px 8px (0, 0, 0, 0.1);
}

.agenda-page-header .ap-title {
  /* border: 1px solid red; */
  gap: 5px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.ap-title .ap-icon {
  background: linear-gradient(to bottom, #6E30DC, #5D12EB);
  color: white;
  border-radius: 8px;
  padding: 5px;
}

.agenda-page-header h2 {
  font-size: 20px;
  font-weight: bold;
  color: #5320be;
  margin: 0px;
}

.agenda-page-header h4 {
  font-size: 15px;
  font-weight: bold;
  color: rgb(100, 99, 99);
  margin: 0px;
}

.agenda-page-header p {
  font-size: 12px;
  color: grey;
}

/* END HEADER  */


/* ACTION  */
.agenda-page-container .agenda-page-action {
  border: 1px solid #ddd;
  border-radius: 10px;
  background-color: white;
  width: 98%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px 20px;
  position: relative;

}

.agenda-page-action .action-left {
  width: 40%;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 4px;
}

.ac-icon {
  font-size: 25px;
  color: grey;
}

.ac-icon:hover {
  background-color: white;
  border: 1px solid white;
}

.action-left input {
  padding: 0px 5px;
  width: 100%;
  font-size: 12px;
  border: 1px solid white;
}

.action-left input:focus {
  outline: none;
}

.agenda-page-action .action-right {
  width: 60%;
  gap: 5px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  /* border: 1px solid red; */
  position: relative;
}

/* DROPDOWN FITURE  */
.dropdown-wrapper {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.dropdown-wrapper p {
  font-size: 12px;
  color: #5e5e5e;
  font-weight: bold;
  margin-right: 10px;
}

.dropdown-toggle {
  padding: 7px 12px;
  background-color: white;
  border: 1px solid #ccc;
  border-radius: 6px;
  cursor: pointer;
  min-width: 180px;
  font-size: 12px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.dropdown-menu {
  position: absolute;
  top: 110%;
  right: 0;
  background-color: white;
  border: 1px solid #ccc;
  width: 65%;
  z-index: 100;
  border-radius: 6px;
  padding: 2px;
  list-style: none;
  max-height: 200px;
  overflow-y: auto;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}

.dropdown-menu li {
  padding: 8px 12px;
  cursor: pointer;
  border: 1px solid white;
  border-radius: 8px;
}

.dropdown-menu li:hover,
.dropdown-menu li.active {
  color: #5320be;
  background-color: #e5d3f8;
  font-weight: bold;
}

/* END DROPDOWN FITURE  */

/* FORM CREATE  */
.action-right .form-agenda {
  border: 1px solid white;
  border-radius: 8px;
  background-color: #6a11cb;
  color: white;
  padding: 7px 10px;
  font-size: 12px;
  gap: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.create-agenda-form {
  position: absolute;
  border: 1px solid #e5d3f8;
  border-radius: 16px;
  background-color: white;
  padding: 10px;
  top: 20%;
  right: 5%;
  z-index: 101;
  width: 30%;
  box-shadow: 0px 4px 8px #5e12eb46;
}

.create-agenda-form .create-agenda-header {
  padding: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-bottom: 1px solid #ddd;
  color: #333;
  background-color: white;
}

.create-agenda-header .agenda-header-left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* border: 1px solid red; */
  gap: 5px;
}

.agenda-header-left .icon-agenda {
  margin-right: 5px;
  color: white;
  padding: 5px;
  border-radius: 6px;
  background: linear-gradient(90deg, #7246eb, #532189)
}

.create-agenda-header h3 {
  font-size: 15px;
  font-weight: bold;
}

.cah-icon {
  cursor: pointer;
}

.create-agenda-form form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  border-radius: 8px;
  border: 1px solid white;
}

form .ap-box {
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
}

.ap-box label {
  width: 100%;
  color: #333;
  font-size: 12px;
}

.ap-box .span-red {
  color: red;
  font-size: 15px;
  margin-left: 5px;
}

.ap-box input,
.ap-box textarea,
.ap-box select {
  padding: 8px;
  width: 100%;
  font-size: 12px;
  border-radius: 8px;
  border: 1px solid #ccc;
}

.ap-box input:hover,
.ap-box textarea:hover,
.ap-box select:hover {
  outline: none;
  border: 1px solid #6a11cb;
  box-shadow: 0px 4px 8px #6b11cb40;
}

.ap-box input:focus,
.ap-box textarea:focus,
.ap-box select:focus {
  outline: none;
  border: 1px solid #6a11cb;
  box-shadow: 0px 4px 8px #6b11cb40;
}

.create-agenda-form select {
  font-size: 12px;
  color: grey;
  padding: 5px;
}

.create-agenda-form button {
  padding: 5px 10px;
  font-size: 12px;
  background-color: #6a11cb;
  color: white;
  border: 1px solid #6a11cb;
  border-radius: 8px;
  cursor: pointer;
  width: 100%;
}

.create-agenda-form button:hover {
  box-shadow: 0px 4px 8px #6b11cb40;
}

/* END ACTION  */

/* AGENDA TABLE  */
.agenda-page-content {
  width: 98%;
  height: 70vh;
  background-color: #f8f9fb;
  border-radius: 10px;
  border: 1px solid #ddd;
  padding: 0px;
  overflow-x: auto;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.05);
}

.empty-agenda-container {
