  left: 0px;
  z-index: 200;
  background-color: rgba(0, 0, 0, 0.155);
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
}

.agenda-modals-box {
  background-color: white;
  width: 40%;
  height: 80%;
  padding: 0px;
  border: 1px solid #ddd;
  border-radius: 12px;
}

.agenda-modals-box .modals-header {
  border-bottom: 1px solid #6a11cb;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  color: white;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: linear-gradient(to right, #5c48f5, #9A40F8);
}

.modals-header .mh-left {
  /* border: 1px solid red; */
  gap: 10px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.mh-left .icon {
  border: 1px solid rgba(255, 255, 255, 0.291);
  border-radius: 10px;
  padding: 5px 8px;
  font-size: 18px;
  background-color: rgba(255, 255, 255, 0.291);
  color: white;
}

.mh-left .title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  gap: 0px;
}

.title input {
  border: none;
  width: 100%;
  font-size: 15px;
  background: linear-gradient(to right, #5c48f5, #9A40F8);
  color: white;
}

.title input:focus {
  outline: none;
}

.title h3 {
  font-weight: bold;
  font-size: 15px;
  margin: 0px;
}

.title p {
  font-size: 10px;
  width: 100%;
  padding: 0px;
}

.close-icon {
  cursor: pointer;
  font-size: 18px;
  filter: drop-shadow(0 0 1px #5320be);
}

.close-icon:hover {
  box-shadow: 0px 4px 8px #6b11cb40;
  filter: drop-shadow(0 0 1px #5320be);
  transform: scale(1.2);
  /*  memperbesar */
  transition: transform 0.2s ease;
  /*  animasi halus */
}

