  /* Sembunyikan teks yang overflow */
  text-overflow: ellipsis;
}

.status {
  border: 1px solid white;
  border-radius: 8px;
  font-size: 10px;
  font-weight: bold;
  padding: 4px 15px;
  width: fit-content;
  color: white;
}

.done-agenda {
  padding: 5px 8px;
  border: 1px solid white;
  border-radius: 8px;
  cursor: pointer;
  font-size: 10px;
  width: fit-content;
  text-align: center;
}

/* Status Badge */
.status-badge {
  display: inline-block;
  padding: 4px 10px;
  font-size: 13px;
  font-weight: 600;
  border-radius: 9999px;
  text-transform: capitalize;
}


/* Action buttons */
.action-buttons {
  display: flex;
  gap: 10px;
}

.action-buttons button {
  background: none;
  border: none;
  cursor: pointer;
  color: #6b7280;
  font-size: 16px;
  transition: all 0.2s ease;
}

.action-buttons button:hover {
  color: #111827;
  transform: scale(1.1);
