.card-user-manager {
  /* padding: 0px 5px; */
  /* border: 1px solid #ddd; */
  background-color: white;
  /* border: 1px solid #e5e7eb; */
  border-radius: 8px;
  width: 100%;
  /* box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05); */
}

.cum-header {
  border-bottom: 1px solid #ddd;
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  background-image: linear-gradient(to right, #2d66e7, #3863e7, #425fe8, #4c5be7, #5557e7, #5d54e7, #6551e7, #6d4de7, #764ae7, #7e47e7, #8743e7, #8f3fe6);
  color: white;
  padding: 5px 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.cum-header .header-left {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  /* border: 1px solid blue; */
  width: 60%;
  gap: 7px;
}

.header-left .left-icon {
  border: 1px solid rgba(255, 255, 255, 0.385);
  border-radius: 8px;
  background-color: rgba(255, 255, 255, 0.385);
  color: #6a11cb;
  padding: 3px;
}

.header-left h5 {
  margin: 0px;
  font-size: 12px;
  font-weight: bold;
}

.cum-icon {
  border-radius: 4px;
  cursor: pointer;
}


.card-user-manager .no-user {
  /* border: 1px solid red; */
  border-radius: 4px;
  background-color: white;
  padding: 10px 8px;
  /* width: 100%; */
  height: fit-content;
  margin: 5px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.no-user p {
  font-size: 10px;
  color: red;
  text-align: center;
  /* width: 90%; */
}

.al-title {
  border: 1px solid blue;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.al-title h5 {
  margin-left: 5px;
  font-size: 10px;
  font-weight: normal;
}

.card-user-manager .assigned-list {
  /* margin-bottom: 1rem; */
  margin: 2px 0px;
  padding: 5px;
  border: 1px solid red;
  list-style: none;
  height: 8vh;
  overflow-y: auto;
}

.card-user-manager .assigned-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.25rem;
  padding: 4px;
  border-radius: 4px;
  border: 1px solid white;
  /* box-shadow: 0px 4px 8px rgba(0, 0, 0,0.1); */
}

.ai-title {
  /* border: 1px solid grey; */
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.ai-title p {
  margin: 0px;
  font-size: 12px;
}

.assigned-item img {
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: 1px solid #6a11cb;
  box-shadow: 0px 4px 8px #5e12eb46;
  background-color: white;
  margin-right: 5px;
}

.card-user-manager .remove-btn {
  color: #ef4444;
  font-size: 0.875rem;
  cursor: pointer;
  margin-left: 5px;
  width: fit-content;
  padding: 2px;
  border: 1px solid white;
  border-radius: 50%;
}

.card-user-manager .remove-btn:hover {
  color: #b91c1c;
}

.dropdown-container {
  position: relative;
  /* margin-top: 1rem; */
  /* border: 1px solid red; */
  padding: 15px;
  gap: 10px;
}

.dropdown-container h5 {
  font-weight: normal;
  font-size: 10px;
  /* margin-bottom: 10px; */
}

.dropdown-container .dropdown-btn {
  background-color: white;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 5px;
  font-size: 10px;
  color: #6a11cb;
  font-weight: normal;
  width: 100%;
}

.dropdown-container .dropdown-btn:hover {
  border: 1px solid #6a11cb;
}


.dropdown-list {
  position: absolute;
  top: 50%;
  right: 0%;
  z-index: 10;
  width: 95%;
  padding: 5px;
  background-color: #fff;
  border: 1px solid #d1d5db;
  /* border: 1px solid blue; */
  border-radius: 0.375rem;
  margin-top: 0.25rem;
  max-height: 15vh;
  overflow-y: auto;
  /* box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1); */
  box-shadow: 0px 4px 8px #5e12eb46;
  list-style: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}

.dl-title {
  /* border: 1px solid grey; */
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.dropdown-list img {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid #ABBAFA;
  /* border: 1px solid #6a11cb; */
  box-shadow: 0px 4px 8px #5e12eb46;
  background-color: white;
  margin-right: 3px;
}

.dropdown-list .select-btn {
  color: green;
  font-size: 0.875rem;
  cursor: pointer;
  margin-left: 5px;
  width: fit-content;
  padding: 2px;
  border: 1px solid white;
  /* border-radius: 50%; */
}

/* .dropdown-list .remove-btn:hover {
    color: #b91c1c;
  }
   */

.dropdown-list li {
  padding: 3px;
  cursor: pointer;
  border: 1px solid #ABBAFA;
  background-color: #EEF2FE;
  /* border: 1px solid red; */
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 90%;
  margin: 3px 0px;
  font-size: 10px;
}

.dropdown-list li:hover {
  background-color: #b3c5fa;
}

.assign-btn {
  margin-top: 0.2rem;
  padding: 5px;
  gap: 5px;
  border-radius: 4px !important;
  color: white !important;
  background: linear-gradient(to right, #2575fc, #6a11cb);
  border: none;
  cursor: pointer;
  display: flex !important;
  align-items: center !important;
  justify-content: center !important;
  cursor: pointer;
  width: 100%;
  font-size: 12px;
}

.assign-btn:hover {
  /* background-color: #2563eb; */
  background: linear-gradient(to right, #6a11cb, #2575fc);
}

.assign-btn:disabled {
  background-color: #9ca3af;
  cursor: not-allowed;
}

