.login-container {
    background-image: linear-gradient(to right, #5211cb, #5711cb, #5b11cb, #6011cb, #6411cb, #6a18ce, #6f1ed0, #7523d3, #7d2dd9, #8637df, #8e40e5, #9649eb);
    width: 100%;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

/* LEFT  */
.login-container .login-content-left {
    /* border: 1px solid red; */
    width: 50%;
    height: 100vh;
    padding: 30px 50px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
}

.login-content-left .h1 {
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: flex-start;
    color: white;
    font-size: 38px;
    font-weight: bold;
    gap: 10px;
    /* border: 1px solid red; */
    margin-bottom: 20px;
}

.h1 .h1-icon {
    font-size: 70px;
    padding: 10px;
    border: 1px solid rgba(255, 255, 255, 0.500);
    border-radius: 16px;
    background-color: rgba(255, 255, 255, 0.500);
    width: fit-content;
}

.login-content-left h4 {
    font-size: 20px;
    color: rgba(245, 245, 245, 0.871);
    margin-bottom: 20px;
}

.login-content-left p {
    font-size: 15px;
    color: rgba(245, 245, 245, 0.62);
}

/* btn lp  */
.login-content-left .btn-lp {
    /* border: 1px solid red; */
    margin-top: 20px;
    width: 100%;
    gap: 10px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    cursor: pointer;
}

.btn-lp h3 {
    font-size: 14px;
    font-weight: bold;
    color: white;
    cursor: pointer;
}

.btn-lp h3:hover {
    text-decoration: underline;
}


/* RIGHT  */
.login-container .login-content-right {
    /* border: 1px solid blue; */
    width: 50%;
    height: 100%;
    padding: 30px !important;
    display: flex;
    align-items: center;
    justify-content: center;
}

.login-content-right .content-box-right {
    box-shadow: 0px 4px 8px #ffffff78;
    background-color: white;
    border: 1px solid white;
    border-radius: 20px;
    padding: 20px;
    width: 80%;
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.content-box-right .box-right {
    /* border: 1px solid red; */
    height: 60%;
    width: 90%;
}

.content-box-right .box-title {
    /* border: 1px solid blue; */
    width: 100%;
    text-align: center;
    /* margin-bottom: 30px; */
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* Tambahkan di sini supaya semua anaknya default align center */
}

.box-title .title-icon {
    background: linear-gradient(90deg, #7246eb, #532189);
    padding: 8px;
    border-radius: 16px;
}

.title-icon .logo-icon {
    color: white;
    font-size: 25px;
}

.box-title h2 {
    color: #333;
    width: 100%;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 10px;
}

.box-title p {
    font-size: 12px;
    color: #5e5e5e;
    margin: 0 auto;
    display: block;
    /* border: 1px solid red; */
}

/* BOX INPUT  */
.box-input {
    /* border: 1px solid red; */
    width: 100%;
    height: 100%;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.box-input input {
    border: 1px solid #eee;
    border-radius: 16px;
    padding: 8px 12px;
    width: 100%;
    font-size: 12px;
    margin: 5px 0px;
    color: #333;
}

.box-input input:hover {
    border: 1px solid #9649eb;
    box-shadow: 0px 4px 8px #6b11cb40;
}

.box-input input:focus {
    outline: none;
    box-shadow: 0px 4px 8px #6b11cb40;
}

.box-input p {
    font-size: 12px;
    color: #6a11cb;
    margin: 15px 0px;
    cursor: pointer;
}

.box-input p:hover {
    text-decoration: underline;
}

.box-input .btn-login {
    border: 1px solid #6a11cb;
    border-radius: 16px;
    background-color: #6a11cb;
    color: white;
    text-align: center;
    width: 100%;
    padding: 8px 12px;
    font-size: 12px;
    cursor: pointer;
}

.box-input .btn-login:hover {
    box-shadow: 4px 4px 8px #6b11cb87;
}

/* PASSWORD BOX  */
.password-wrapper {
    margin-top: 10px;
    /* border: 1px solid red; */
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.password-wrapper input {
    width: 100%;
    padding-right: 40px;
    /* biar nggak ketimpa icon mata */
    border: 1px solid #eee;
    border-radius: 16px;
    font-size: 12px;
    margin: 5px 0px;
    color: #333;
}

.toggle-password {
    position: absolute;
    right: 10px;
    cursor: pointer;
    color: #888;
}

/* END PASSWORD BOX  */

/* ANOTHER LOGIN  */
.another-login {
    /* border: 1px solid magenta; */
    width: 100%;
    padding: 10px;
    gap: 10px;
    margin: 0px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.another-login .google {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #eee;
    border-radius: 16px;
    padding: 5px 8px;
    gap: 5px;
    cursor: pointer;
}

.another-login .ios {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    border: 1px solid #eee;
    padding: 5px 8px;
    border-radius: 16px;
    gap: 5px;
    cursor: pointer;
}

.another-login .google:hover,
.another-login .ios:hover {
    box-shadow: 4px 4px 8px #6b11cb87;
}

/* FOOTER BOX  */
.box-footer {
    width: 100%;
    text-align: center;
    margin-top: 15px;
}

.box-footer h5 {
    font-size: 13px;
    font-weight: bold;
    color: #333;
}

.box-footer span {
    color: #6a11cb;
    cursor: pointer;
}

.box-footer p {
    font-size: 13px;
    padding: 0px;
    margin: 0px;
}

.box-footer span:hover {
    text-decoration: underline;
}


/* MEDIA QUERY  */
/* Extra large devices (large desktops, 1200px) */
@media (min-width: 992px) and (max-width:1400px) {
    .login-container .login-content-right {
        padding: 10px;
    }

    .login-content-right .content-box-right {

        height: 100%;
    }
}

/* Desktop ( 992px) */
@media (min-width: 992px) {
    .login-container .login-content-left {
        width: 50%;
    }

    .login-container .login-content-right {
        width: 50%;
        /* border: 1px solid red; */
        padding: 10px;
        height: 100%;
    }

    .login-content-right .content-box {
        width: 90%;
        height: 90vh;
        padding: 20px !important;
        gap: 20px;
        align-items: center;
    }

    .box-footer p {
        display: none;
    }
}

/* Medium devices (tablets, 768px) */
@media (min-width: 768px) and (max-width: 991.98px) {

    .login-container .login-content-left {
        width: 40%;
        padding: 10px;
        padding-left: 20px !important;
        /* border: 1px solid red; */
    }

    .login-content-left h4 {
        font-size: 15px !important;
    }

    .login-content-left p {
        display: none;
    }


    .login-container .login-content-right {
        width: 60%;
        /* border: 1px solid red; */
        padding: 10px;
    }


    .login-content-right .content-box {
        width: 90%;
        height: 90%;
        padding: 20px !important;
        /* gap: 20px; */
    }

    .content-box .box-title {
        text-align: center;
        height: 20%;
    }

    .another-login {
        /* border: 1px solid blue; */
        margin: 0;
    }


    .box-footer {
        margin-top: 0px;
    }

    .box-footer p {
        display: none;
    }
}

/* CSS tablet atau iPad */


/* Tablet ( 576px dan < 992px) */
@media (min-width: 576px) and (max-width: 767.98px) {
    .login-container .login-content-left {
        display: none;
    }

    .login-container .login-content-right {
        width: 100% !important;
        padding: 10px;
        height: 100%;
    }

    .login-content-right .content-box {
        width: 80%;
        height: 90vh;
        padding: 20px !important;
        gap: 20px;
        align-items: center;
    }

    .content-box .box-title {
        text-align: center;
        /* height: 20%; */
    }

    .box-input {
        /* border: 1px solid blue; */
        width: 80% !important;
    }

    .another-login {
        /* border: 1px solid blue; */
        margin: 0;
        width: 80% !important;
    }


    .box-footer {
        margin-top: 0px !important;
    }
}

/* Mobile ( < 576px) */
@media (max-width: 575.98px) {
    .login-container .login-content-left {
        display: none;
    }

    .login-container .login-content-right {
        width: 100%;
        padding: 15px 25px !important;
        height: 100%;
    }

    .login-content-right .content-box {
        width: 95% !important;
        padding: 25px !important;
        gap: 20px;
        height: 90vh !important;
    }

    .content-box .box-title {
        text-align: center;
        /* height: 20%; */
    }

    .another-login {
        /* border: 1px solid blue; */
        margin: 0;
    }


    .box-footer {
        margin-top: 0px !important;
    }
}


/* HEIGHT BREAKPOIN  */
/* HEIGHT BREAKPOIN  */
/* HEIGHT BREAKPOIN  */
@media screen and (max-height: 600px) {
    .login-container .login-content-right {
        /* border: 1px solid red; */
        padding: 5px !important;
    }

    .login-content-right .content-box-right {
        height: 95% !important;
    }

    .content-box-right .box-right {
        width: 100%;
    }

    .another-login .google,
    .another-login .ios {
        font-size: 12px;
    }
}


/* END MEDIA QUERY  */